import React, {useState} from 'react';

function PushNotification() {
  const [subscription, setSubscription] = useState('');

  

  const showNotification = () => {
    Notification.requestPermission(function(status) {
      console.log('Notification permission status:', status);
      if (status === 'granted') {
        navigator.serviceWorker.ready.then(function(registration) {
          registration.showNotification('Vibration Sample', {
            body: 'Buzz! Buzz!',
            icon: '/android-chrome-192x192.png',
            vibrate: [100, 50, 100],
            data: {
              dateOfArrival: Date.now(),
              primaryKey: 1
            },
            actions: [
              {action: 'explore', title: 'Explore this new world'},
              {action: 'close', title: 'Close notification'},
            ]
          });
        });
      }
    });
  }

  function urlB64ToUint8Array(base64String) {
    const padding = '='.repeat((4 - base64String.length % 4) % 4);
    const base64 = (base64String + padding)
      .replace(/\-/g, '+')
      .replace(/_/g, '/');
  
    const rawData = window.atob(base64);
    const outputArray = new Uint8Array(rawData.length);
  
    for (let i = 0; i < rawData.length; ++i) {
      outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
  }
  
  function subscribeToPushManager(registration) {
    console.log('subscribe to push manager', registration.pushManager);
    registration.pushManager.subscribe({
      userVisibleOnly: true,
      applicationServerKey: urlB64ToUint8Array("BL44QZGCmHVWyIs-czW26GZAPse0K2pS4-jtFvH4z72Uuo2W6G95NYOvv-y_bYHnGBYRb3oju_Hgl2c0Xtul4Gc")
    }).then(subscription => {
      const subscriptionJson = JSON.stringify(subscription);
      console.log('subscription json', subscriptionJson);
      setSubscription(subscriptionJson);
    }).catch(function (err) {
      console.log('error in subcription: ', err);
    });
  }

  const showSubscription = () => {
      Notification.requestPermission(function(status) {
      console.log('Notification permission status:', status);
        if (status === 'granted') {
          navigator.serviceWorker.ready.then(registration => {
          registration.pushManager.getSubscription().then(sub => {
            console.log('has already a subscription', sub);

            if (sub) {
              sub.unsubscribe().then(unsubscribed => {
                if (unsubscribed) {
                  console.log('unsubscribed', unsubscribed);
                  subscribeToPushManager(registration);
                }
              })
            } else {
              subscribeToPushManager(registration);
            }
          });      
        });
      }
    });
  }

  return (
      <div className='tc'>
        <button onClick={showNotification}>Display notification from client</button>
        <button onClick={showSubscription}>Display subscription for server</button>
        <input type="text" class="w-100" value={subscription} />
        <textarea row="10" col="10">{subscription}</textarea>
      </div>
  )
}

export default PushNotification