import React, { useEffect } from 'react';

function Posts() {
  useEffect(() => {
    fetch('https://jsonplaceholder.typicode.com/posts')
      .then(response => response.json())
      .then(data => console.log("GET posts", data))
  }, [])

  const newPost = () => {
    fetch('https://jsonplaceholder.typicode.com/posts', {
      method: 'POST',
      body: JSON.stringify({
        title: 'foo',
        body: 'bar',
        userId: 1
      }),
      headers: {
        "Content-type": "application/json; charset=UTF-8"
      }
    })
    .then(response => response.json())
    .then(json => console.log("POST post", json))
  }

  return (
      <div className='tc'>
        <h1>Posts</h1>

        <button onClick={newPost}>POST A NEW ONE</button>
      </div>
  )
}

export default Posts